<template>
  <div class="home">
    <!-- "CREATE STORY UNIT" FORM -->
    <div
      class="form _shadow-1"
      :class="story.createStoryUnit.formActive && 'form_active'"
    >
      <div class="_ml_3 _mb_3">
        <mark class="mark mark_size_middle _mr_2 _d_ib">Type:</mark
        >{{ story.createStoryUnit.data.type }}
      </div>
      <div class="_d_ib _ml_3">
        <mark class="mark mark_size_middle _mr_2 _d_ib _mb_2">Tags:</mark>
        <transition-group name="_list">
          <div
            class="tag _mr_2 _mb_2"
            v-for="(k, i) in story.createStoryUnit.data.tags"
            :key="k"
            @dblclick="story.createStoryUnit.data.tags.splice(i, 1)"
          >
            {{ k }}
          </div>
        </transition-group>
      </div>
      <br />
      <div class="_d_ib _mb_3">
        <select
          class="select select_size_middle _ml_3 _w_100"
          size="4"
          v-model="story.createStoryUnit.newTag"
        >
          <option
            v-for="k in setTags"
            :key="k"
            @dblclick="
              story.createStoryUnit.newTag != '' &&
                story.createStoryUnit.data.tags.push(
                  story.createStoryUnit.newTag
                ) &&
                (story.createStoryUnit.newTag = '')
            "
          >
            {{ k }}
          </option>
        </select>
        <span class="_d_ib _ml_3 _mt_2">OR (if new tag)</span>
        <input
          type="text"
          class="input _ml_3 _mt_2"
          v-model="story.createStoryUnit.newTag"
        />
        <button
          class="button _ml_3 _mt_2"
          @click="
            story.createStoryUnit.newTag != '' &&
              story.createStoryUnit.data.tags.push(
                story.createStoryUnit.newTag
              ) &&
              (story.createStoryUnit.newTag = '')
          "
        >
          Add tag
        </button>
      </div>
      <mark class="mark mark_size_middle _d_ib _ml_3">Value:</mark>
      <input
        type="number"
        class="input input_theme_secondary _ml_3 _mt_2"
        placeholder="Number"
        v-model="story.createStoryUnit.data.value"
        min="1"
      />
      <button
        class="button button_size_large button_theme_primary _ml_4 _mt_3"
        @click="addStoryUnit"
      >
        Add
      </button>
    </div>
    <!-- "CREATE STORY UNIT" FORM BG -->
    <div
      class="form-bg"
      :class="story.createStoryUnit.formActive && 'form-bg_active'"
      @click="story.createStoryUnit.formActive = false"
    ></div>
    <!-- "CREATE STORY UNIT" BUTTON -->
    <div class="create-story-unit button-group _d_ib _shadow-3">
      <button
        type="button"
        class="button button_size_large"
        @click="
          (story.createStoryUnit.formActive = true) &&
            (story.createStoryUnit.data.type = 'income')
        "
      >
        Income
      </button>
      <div
        class="button button_size_large"
        @click="
          (story.createStoryUnit.formActive = true) &&
            (story.createStoryUnit.data.type = 'expense')
        "
      >
        Expense
      </div>
    </div>
    <div class="wrap wrap_page">
      <div class="wrap__inner _pl_5">
        <!-- SAVED THIS WEKK -->
        <Bubble
          :interest="saveThisWeekInterest"
          text="Saved this week as a interest"
          class="_mt_5 _d_ib"
          @click.native="(story.type = 'income') && (story.range = 'w')"
        />
        <!-- EXPENSES TODAY -->
        <Bubble
          :interest="expensesTodayInterest"
          text="Expenses today"
          class="_ml_4 _mt_5 _d_ib"
          form="long"
          theme="secondary"
          @click.native="(story.type = 'expense') && (story.range = 'd')"
        />
        <div class="wrap _mt_5">
          <div class="balance">
            <h4>
              <mark class="mark mark_size_large">
                Balance
                {{
                  getSum(getData("i", "income")) -
                    getSum(getData("i", "expense"))
                }}$
              </mark>
            </h4>
          </div>
        </div>
        <!-- MAIN DATA -->
        <div class="wrap _mt_4">
          <h5>
            Income today
            <mark class="mark mark_size_small"> {{ incomeToday }}$ </mark>
          </h5>
          <br />
          <br />
          <h6>
            expenses today
            <mark class="mark mark_size_small"> {{ expensesToday }}$ </mark>
          </h6>
          <br />
          <br />
          <h5>
            Last 30 days revenue
            <mark class="mark mark_size_small">
              {{ getSum(getData("m", "income")) }}$
            </mark>
          </h5>
          <br />
          <br />
          <h6>
            Last 30 days expenses
            <mark class="mark mark_size_small">
              {{ getSum(getData("m", "expense")) }}$
            </mark>
          </h6>
        </div>
      </div>
      <div class="wrap__inner _pl_4">
        <h3 class="_pl_4">{{ story.rangeAssn[story.range] }} STORY</h3>
        <div class="wrap _mt_4">
          <!-- STORY SORT -->
          <div class="wrap _mb_2">
            <div class="_d_ib _ws_nw _mr_3 _mb_2">
              <mark class="mark mark_size_small">
                Range:
              </mark>
              <select class="select _ml_2" v-model="story.range">
                <option
                  v-for="(option, i) in story.rangeAssn"
                  :value="i"
                  :key="i"
                >
                  {{ story.rangeAssn[i] }}
                </option>
              </select>
            </div>
            <div class="_d_ib _mb_2">
              <mark class="mark mark_size_small">
                Sort:
              </mark>
              <select class="select _ml_2" v-model="story.sort">
                <option value="date">
                  date
                </option>
                <option value="value">
                  value
                </option>
              </select>
              <div class="button-group _d_ib _ml_2">
                <button
                  type="button"
                  class="button"
                  :class="story.newer && 'button_theme_secondary'"
                  @click="story.newer = true"
                >
                  <i
                    class="button__icon button__icon_only fas fa-sort-amount-down"
                  ></i>
                </button>
                <button
                  type="button"
                  class="button"
                  :class="!story.newer && 'button_theme_secondary'"
                  @click="story.newer = false"
                >
                  <i
                    class="button__icon button__icon_only fas fa-sort-amount-up"
                  ></i>
                </button>
              </div>
            </div>
            <div class="_d_ib _mb_2">
              <mark class="mark mark_size_small">
                Type:
              </mark>
              <select class="select _ml_2" v-model="story.type">
                <option :value="true">
                  All
                </option>
                <option value="income">
                  income
                </option>
                <option value="expense">
                  expense
                </option>
              </select>
            </div>
          </div>
          <!-- STORY -->
          <transition-group name="_list" tag="div" class="wrap story">
            <div
              class="story-unit _mt_3 _list-item"
              :class="'story-unit_type_' + storyUnit.type"
              v-for="storyUnit in setStory"
              transition="thing"
              :key="storyUnit.id"
            >
              <div class="story-unit__value">{{ storyUnit.value }}$</div>
              <div class="story-unit__tags">
                <span
                  class="tag _mr_2"
                  v-for="(tag, k) in storyUnit.tags"
                  :key="k"
                >
                  {{ tag }}
                </span>
              </div>
              <div class="story-unit__date _ml_5 _pl_2 _mt_2">
                {{
                  storyUnit.date.getFullYear() +
                    "." +
                    storyUnit.date.getMonth() +
                    "." +
                    storyUnit.date.getDate()
                }}
              </div>
            </div>
          </transition-group>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Bubble from "@/components/Bubble.vue";
import { mapGetters } from "vuex";

export default {
  name: "Home",
  components: {
    Bubble
  },
  data() {
    return {
      story: {
        type: true,
        newer: true,
        rangeAssn: {
          m: "THIS MONTH",
          w: "THIS WEEK",
          d: "TODAY"
        },
        range: "w",
        sort: "date",
        createStoryUnit: {
          formActive: false,
          newTag: "",
          data: {
            type: "",
            tags: [],
            value: 0
          }
        }
      }
    };
  },
  computed: {
    ...mapGetters(["getSum", "getData", "getInterest", "sortData", "getTags"]),
    saveThisWeekInterest() {
      let saveThisWeek =
        this.getSum(this.getData("w", "income")) -
        this.getSum(this.getData("w", "expense"));

      let incomeThisWeek = this.getSum(this.getData("w", "income"));
      let saveThisWeekInterest = this.getInterest(incomeThisWeek, saveThisWeek);

      return saveThisWeekInterest.toFixed(2);
    },
    expensesTodayInterest() {
      let incomeToday = this.incomeToday,
        expensesToday = this.expensesToday,
        expensesTodayInterest = this.getInterest(incomeToday, expensesToday);

      return expensesTodayInterest.toFixed(2);
    },
    incomeToday() {
      return this.getSum(this.getData("d", "income"));
    },
    expensesToday() {
      return this.getSum(this.getData("d", "expense"));
    },
    setStory() {
      return this.sortData(
        this.getData(this.story.range, this.story.type),
        this.story.newer,
        this.story.sort
      );
    },
    setTags() {
      return this.getTags(
        this.story.createStoryUnit.data.tags.length,
        this.story.createStoryUnit.data.tags.slice(-1)[0],
        this.story.createStoryUnit.data.type
      );
    }
  },
  methods: {
    addStoryUnit() {
      if (
        this.story.createStoryUnit.data.value > 0 &&
        this.story.createStoryUnit.data.tags.length != 0
      ) {
        this.story.createStoryUnit.data.date = new Date();

        this.$store.state.settings.story.lastId += 1;
        this.story.createStoryUnit.data.id = this.$store.state.settings.story.lastId;

        this.$store.state.story.push(this.story.createStoryUnit.data);

        this.story.createStoryUnit = {
          formActive: false,
          newTag: "",
          data: {
            type: "",
            tags: [],
            value: 0
          }
        };
      }
    }
  }
};
</script>

<style lang="scss" scoped>
// Imports

@import "../assets/stylesheets/abstracts/variables";

