export default new Vuex.Store({
  state: {
    settings: {
      story: {
        lastId: 12
      }
    },
    story
  },
  mutations: {},
  actions: {},
  getters: {
    getData: state => (
      range: string = "w",
      type: string | boolean = true
    ): Array<storyUnit> => {
      let rangeAssn: { [key: string]: number } = {
        d: 1,
        w: 7,
        m: 30,
        i: Infinity
      };

      let response: Array<storyUnit> = [];

      state.story.map(i => {
        if (i.type == type || (typeof type === "boolean" && type)) {
          let dateConverge: boolean =
            Math.ceil(
              Math.abs(i.date.getTime() - new Date().getTime()) /
                (1000 * 60 * 60 * 24)
            ) <= rangeAssn[range];

          if (dateConverge) {
            response.push(i);
          }
        }
      });

      return response;
    },
    getSum: () => (data: Array<storyUnit>): number => {
      let sum: number = 0;

      data.map((i): void => {
        sum += Number(i.value);
      });

      return sum;
    },
    getInterest: () => (total: number, part: number): number => {
      return (part * 100) / total;
    },
    sortData: () => (
      data: Array<storyUnit>,
      newer: Boolean = true,
      sort: string = "date"
    ): Array<storyUnit> => {
      data.sort((a: storyUnit, b: storyUnit): any => {
        if (sort == "date") {
          return newer
            ? <any>new Date(b.date) - <any>new Date(a.date)
            : <any>new Date(a.date) - <any>new Date(b.date);
        } else if (sort == "value") {
          return newer ? b.value - a.value : a.value - b.value;
        }
      });

      return data;
    },
    getTags: store => (
      stage: number,
      prevStage: string,
      type: string
    ): Array<string> => {
      let tags: Array<string> = [];

      store.story.map(storyUnit => {
        storyUnit.type == type &&
          storyUnit.tags[stage] &&
          !tags.includes(storyUnit.tags[stage]) &&
          (stage == 1
            ? tags.push(storyUnit.tags[stage])
            : storyUnit.tags[stage - 1] == prevStage &&
              tags.push(storyUnit.tags[stage]));
      });

      return tags;
    }
  }
});
